@font-face {
  font-family: "DIN Bold";
  src: url(../fonts/DIN-Bold.ttf) format("truetype");
}
body {
  background: #ff6347;
  font-family: "DIN Bold", sans-serif;
}
body h1 {
  background: #f00;
}

.d-flex {
  display: flex;
  align-items: center;
}

.icon {
  margin: 0 10px;
  width: 90px;
  height: auto;
}

.spotify {
  height: 100px;
  width: auto;
}

div img {
  height: 100px;
  margin: 0 10px;
}